// store/useUserStore.ts

import { create } from "zustand";
import { retrieveLaunchParams } from "@telegram-apps/sdk";
import photoUrl from "../assets/images/avatar.png";
import { User as AppUser } from "../types/User";
import { User as TgUser } from "@telegram-apps/types"; //     Telegram SDK

//   
interface UserState {
  user: AppUser | null;
  isLoading: boolean;
  error: string | null;
  initializeUser: () => Promise<void>;
}

//   
const defaultUser: AppUser = {
  id: 2281337,
  first_name: "Muamee",
  last_name: "4ever",
  username: "test_user",
  language_code: "ru",
  is_premium: false,
  photo_url: photoUrl, //    
};

//    initDataRaw    
const verifyInitData = async (initDataRaw: string): Promise<AppUser | null> => {
  try {
    const response = await fetch(
      "https://your-server.com/api/verify-init-data",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `tma ${initDataRaw}`,
        },
      }
    );

    if (!response.ok) {
      throw new Error("   init   .");
    }

    const data = await response.json();

    // ,        
    return data.user as AppUser;
  } catch (error) {
    console.error("  init :", error);
    return null;
  }
};

//   Zustand
export const useUserStore = create<UserState>((set) => ({
  user: null,
  isLoading: false,
  error: null,

  initializeUser: async () => {
    set({ isLoading: true, error: null });

    if (process.env.NODE_ENV === "development") {
      //      
      console.log("    ().");
      set({ user: defaultUser, isLoading: false });
      return;
    }

    // ,   Telegram WebApp
    const tg = window.Telegram?.WebApp;
    if (!tg) {
      console.warn(
        "Telegram WebApp .    ."
      );
      set({ user: defaultUser, isLoading: false });
      return;
    }

    //     Telegram WebApp
    const { initDataRaw, initData } = retrieveLaunchParams();
    console.log("Init Data Raw:", initDataRaw);
    console.log("Init Data:", initData);

    if (!initDataRaw) {
      console.warn(
        "Init Data Raw .    ."
      );
      set({ user: defaultUser, isLoading: false });
      return;
    }

    //  initDataRaw        
    const verifiedUser = await verifyInitData(initDataRaw);

    if (verifiedUser) {
      set({ user: verifiedUser, isLoading: false });
      console.log("  .");
    } else {
      //    ,      
      console.warn(
        " init   .    ."
      );
      set({ user: defaultUser, isLoading: false });
      set({ error: "    ." });
    }
  },
}));
